				this.markers = [];
				$(".amap-marker-content").empty();
				if (this.flags && this.flag) {
					this.sajax(3);
				}
				if (!this.flags && this.flag) {
					this.sajax(1);
				}
				if (this.flags && !this.flag) {
					this.sajax(2);
				}
				if (!this.flags && !this.flag) {
					this.sajax(0);
				}
